import React from 'react';
import CompteList from './AccountCardList/AccountCard';
import BenefList from './BenefCardList/BenefCard';
import { t } from '../../i18n';

const beneficiariesStep = ({
  onAccountChange,
  onBeneficiaryChange,
  accountsList,
  beneficiariesList,
  showAlertEnabled,
  selectedBeneficiary,
  selectedAccount,
  handleBeneficiary,
}) => {
  const selectedAccountHandler = childData => {
    onAccountChange(childData);
  };
  const selectedBenefHandler = childData => {
    onBeneficiaryChange(childData);
  };
  return (
    <div className="panel-body">
      <div className="wizard-progress_content">
        <div className="wizard-progress_step step-content active">
          <div className="row mx-auto">
            <CompteList
              benefCardTitle={t('Components:chooseMyAccountToDebit')}
              accountList={accountsList}
              account={selectedAccountHandler}
              selectedAccount={selectedAccount}
            />
            <BenefList
              onclick={handleBeneficiary}
              showAlert={showAlertEnabled}
              text={[
                t('Components:chooseBeneficiary'),
                t('Components:or'),
                t('Components:addBeneficiary'),
              ]}
              benef={selectedBenefHandler}
              row={beneficiariesList}
              selectedBeneficiary={selectedBeneficiary}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default beneficiariesStep;
